@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
:root{--primary: rgb(0, 157, 250);--red: rgb(255, 82, 73);--border: rgba(0, 0, 0, 0.068)}*{margin:0;padding:0;box-sizing:border-box}body{min-height:85vh;display:flex;align-items:center;justify-content:center;font-family:'Roboto', sans-serif}input,button{padding:15px;border-radius:30px;border:1px solid rgba(0,0,0,0.308);background-color:white;outline:none}.input-submit{background-color:rgb(0, 157, 250);background-color:var(--primary);color:white;font-weight:600;margin-top:1.7rem;margin-bottom:3.2rem;border:none}button,.input-submit{cursor:pointer}ul{list-style-type:none}a{color:black;text-decoration:none}.link_icon{font-size:2rem;margin-bottom:10px;color:rgb(0, 157, 250);color:var(--primary)}.App{width:100vw;min-height:550px;margin:0 auto;border-radius:10px;border:1px solid rgba(0, 0, 0, 0.068);border:1px solid var(--border);padding:0.7rem;padding-top:0;background-color:white;position:relative}@media (min-width: 500px){.App{width:500px;padding:2rem;padding-top:0}}.App .init-message{position:absolute;top:50%;left:50%;transform:translate(-50%, -50%)}.header{display:flex;align-items:center;justify-content:center;height:100px;border-bottom:1px solid rgba(0, 0, 0, 0.068);border-bottom:1px solid var(--border);margin-bottom:3rem}.header .link_home{display:flex;align-items:center;justify-content:center}.header .link_home .link_icon{font-size:2.5rem}.auth-form{margin-bottom:60px}.auth-form form{display:flex;flex-direction:column;flex:1 1}.auth-form form input:nth-child(1){margin-bottom:7px}.auth-form .auth-switch{background-color:white;color:rgb(0, 157, 250);color:var(--primary);border:1px solid rgb(0, 157, 250);border:1px solid var(--primary);display:block;text-align:center}.auth-provider{display:flex;flex-direction:column;justify-content:space-between;height:100px}@media (min-width: 400px){.auth-provider{flex-direction:row;height:auto}}.auth-provider .provider_btn{display:flex;justify-content:center}@media (min-width: 400px){.auth-provider .provider_btn{flex:0.9 1}}.auth-provider .provider_btn .social-icon{margin-right:0.3rem}.auth-provider .provider_btn.google{margin-right:5px}.auth-provider .provider_btn.google:hover{background-color:#f4f4f4}.auth-provider .provider_btn.facebook{background-color:#385298;border:none;color:white}.auth-provider .provider_btn.facebook:hover{background-color:rgba(56,82,152,0.863)}.auth-provider .provider_btn.facebook .social-icon{color:white}nav{display:flex;align-items:center;justify-content:center;margin-bottom:3rem}nav .link_profile{display:flex;flex-direction:column;align-items:center}.tweet-form-container{width:100%;position:relative;margin-bottom:2rem}.tweet-form-container .tweet-form{width:100%}.tweet-form-container .tweet-form .row-1{display:flex;position:relative}.tweet-form-container .tweet-form .row-1 .input-text{flex:1 1;padding-right:60px;border:1px solid rgb(0, 157, 250);border:1px solid var(--primary)}.tweet-form-container .tweet-form .row-1 .input-submit{position:absolute;right:0;background-color:rgb(0, 157, 250);background-color:var(--primary);height:100%;width:50px;padding:10px 0px;text-align:center;border-radius:30px;color:white;border:none;margin:0}.tweet-form-container .tweet-form .row-2 .add-photo-label{display:flex;align-items:center;justify-content:center;width:100%;height:80px}.tweet-form-container .tweet-form .row-2 .add-photo-label span{margin-right:0.9rem}.tweet-form-container .tweet-form .row-2 .add-photo-label .add-photo-icon{color:rgb(0, 157, 250);color:var(--primary);font-size:1.5rem;cursor:pointer}.tweet-form-container .tweet-form .row-2 .add-photo-input{display:none}.tweet-form-container .photo-snippet-container{display:flex;justify-content:center;align-items:center;border-radius:10px;border:1px solid rgba(0, 0, 0, 0.068);border:1px solid var(--border);padding:0.2rem;position:relative}.tweet-form-container .photo-snippet-container img{width:50%;height:150px}.tweet-form-container .photo-snippet-container button{position:absolute;right:10px;bottom:10px;padding:0.4rem 1rem}.tweets .tweet{margin-bottom:0.8rem;min-height:2rem;display:flex;flex-direction:column;justify-content:center;border-radius:10px;border:1px solid rgba(0, 0, 0, 0.068);border:1px solid var(--border);padding:0.7rem}.tweets .tweet .row-1 img{display:block;width:60%;height:200px;margin:0 auto 2rem auto}.tweets .tweet .row-2{display:flex;align-items:center}.tweets .tweet .row-2 .tweet__text{flex:1 1}.tweets .tweet .row-2 .icon{cursor:pointer;color:rgb(0, 157, 250);color:var(--primary)}.tweets .tweet .row-2 .delete-icon{margin:0 0.5rem 0 1rem}.profile{display:flex;flex-direction:column}.profile form{height:120px;display:flex;flex-direction:column;justify-content:space-around}.profile .line{display:block;width:100%;height:1px;background-color:rgba(0,0,0,0.068);margin:2.5rem 0 3rem 0}.profile .logout-btn{border:none;background-color:rgb(255, 82, 73);background-color:var(--red);color:white}

